.headsectionsecondary {
  margin-top: 1em;
  margin-bottom: 0.5em;
  width: 100%;
  height: 225px;
  border-radius: 10px;
  object-fit: cover;
  object-position: center;
  filter: brightness(0.7);
}

@media screen and (min-width: 750px) {
  .headsectionsecondary {
    margin-top: 1.5em;
    margin-bottom: 0.5em;
    border-radius: 25px;
  }
}
